import connectComponent from 'src/utils/connectComponent.js';
import {startFetchingComponent, endFetchingComponent, shouldComponentMount} from 'src/utils/route-utils';export default [
    {
        path: '/dev/menus',
        getComponent: (nextState, cb) => {startFetchingComponent();require.ensure([], (require) => {if (!shouldComponentMount(nextState)) return;endFetchingComponent();cb(null, connectComponent(require('./menu/Menu')));});},
    },
];



